<template>
  <div class="login">
    <el-form :rules="login" ref="loginForm" class="login-container" :model="loginForm" label-position="left"
            label-width="0px" v-loading="loading">
      <h3 class="login_title">绩效考核系统登录</h3>
      <el-form-item prop="account">
        <el-input type="text" v-model="loginForm.account" auto-complete="off" placeholder="账号"></el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input type="password" v-model="loginForm.password" auto-complete="off" placeholder="密码"></el-input>
      </el-form-item>
      <el-checkbox class="login_remember" v-model="checked" label-position="left">记住密码</el-checkbox>
      <el-form-item style="width: 100%">
        <el-button type="primary" @click="submitClick" style="width: 100%">登录</el-button>
      </el-form-item>
    </el-form>
  </div>
</template> 
<script>  
import axios from 'axios'
  export default{
    name:'Login',
    data(){
      return {
        login: {
          account: [
            {required: true, message: '请输入用户名', trigger: 'blur'},
          ],
          password: [
            {required: true, message: '请输入密码', trigger: 'blur'},
          ]
        },
        checked: false,
        loginForm: {
          account: '',
          password: ''
        },
        loading: false
      }
    },
    methods: {
      submitClick (loginForm) {
        this.$refs.loginForm.validate(valid => {
          if (valid) {
            const _this=this;
            _this.loading = true
            //Post请求有问题404需后端允许跨域？
            axios.get('/api/login.json',_this.loginForm)
              .then(function (res) {
                _this.$router.push({ path: '/home' })
              })
              .catch(function (error) {
                console.log(error)
              })
          } else {
            console.log('error submit!!')
            return false
          }
        })
      },
      resetForm(loginForm) {
				this.$refs.loginForm.resetFields()
			}
    }
  }
</script>
<style>
   .login-container {
    border-radius: 15px;
    background-clip: padding-box;
    margin: 180px auto;
    width: 350px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
  }

  .login_title {
    margin: 0px auto 40px auto;
    text-align: center;
    color: #505458;
  }

  .login_remember {
    margin: 0px 0px 35px 0px;
    text-align: center;
  }
</style>



